import React, { useMemo, useState, useCallback } from 'react';
import { Line } from 'react-chartjs-2';
import { Chart, registerables } from 'chart.js';
import { Helmet } from 'react-helmet';
Chart.register(...registerables);

// Custom hook for chart animation throttling based on device performance
const useAdaptiveAnimation = () => {
  const [duration, setDuration] = useState(1000);
  
  React.useEffect(() => {
    // Check if device is low-end
    const isLowEndDevice = () => {
      const memory = navigator.deviceMemory;
      const connection = navigator.connection?.effectiveType;
      
      if (memory && memory < 4) return true;
      if (connection && (connection === 'slow-2g' || connection === '2g' || connection === '3g')) return true;
      
      return false;
    };
    
    setDuration(isLowEndDevice() ? 0 : 1000);
  }, []);
  
  return duration;
};

const RateTable = ({ selectedCity, selectedState, eggRates }) => {
  const [showFullHistory, setShowFullHistory] = useState(false);
  const animationDuration = useAdaptiveAnimation();
  
  // Memoize sorted rates to avoid recalculating on every render
  const sortedRates = useMemo(() => {
    if (!eggRates || eggRates.length === 0) return [];
    return [...eggRates].sort((a, b) => new Date(b.date) - new Date(a.date));
  }, [eggRates]);
  
  // Memoize filtered rates for historical display
  const { filteredRates, allFilteredRates } = useMemo(() => {
    if (!sortedRates || sortedRates.length === 0) {
      return { filteredRates: [], allFilteredRates: [] };
    }
    
    // Filter rates to only show the last 10 days by default
    const tenDaysAgo = new Date();
    tenDaysAgo.setDate(tenDaysAgo.getDate() - 10);
    
    const filtered = sortedRates.filter(rate => {
      const rateDate = new Date(rate.date);
      return rateDate >= tenDaysAgo;
    });
    
    // Also prepare all history rates for optional display
    const thirtyDaysAgo = new Date();
    thirtyDaysAgo.setDate(thirtyDaysAgo.getDate() - 30);
    
    const allFiltered = sortedRates.filter(rate => {
      const rateDate = new Date(rate.date);
      return rateDate >= thirtyDaysAgo;
    });
    
    return { filteredRates: filtered, allFilteredRates: allFiltered };
  }, [sortedRates]);
  
  // Toggle function for history display
  const toggleHistoryDisplay = useCallback(() => {
    setShowFullHistory(prev => !prev);
  }, []);
  
  // Display rates based on user selection
  const displayRates = showFullHistory ? allFilteredRates : filteredRates;
  
  // Memoize chart data to prevent unnecessary recalculations
  const chartData = useMemo(() => {
    if (displayRates.length === 0) {
      return {
        labels: [],
        datasets: [{ label: 'Egg Rate (₹)', data: [], fill: false, borderColor: 'rgb(75, 192, 192)', tension: 0.1 }]
      };
    }
    
    // Get dates and rates for chart
    const dates = displayRates.map(rate => {
      const date = new Date(rate.date);
      return date.toLocaleDateString('en-US', { month: 'short', day: 'numeric' });
    }).reverse();
    
    const rates = displayRates.map(rate => parseFloat(rate.rate)).reverse();
    
    return {
      labels: dates,
      datasets: [
        {
          label: 'Egg Rate (₹)',
          data: rates,
          fill: false,
          borderColor: 'rgb(75, 192, 192)',
          tension: 0.1
        }
      ]
    };
  }, [displayRates]);
  
  // Memoize chart options for stable reference
  const chartOptions = useMemo(() => {
    return {
      animation: {
        duration: animationDuration
      },
      responsive: true,
      maintainAspectRatio: false,
      plugins: {
        legend: {
          display: true,
          position: 'top'
        },
        tooltip: {
          mode: 'index',
          intersect: false
        }
      },
      scales: {
        y: {
          beginAtZero: false,
          ticks: {
            // Ensure enough precision without overkill
            precision: 1
          }
        }
      }
    };
  }, [animationDuration]);
  // Get latest rate and calculate tray price
  const latestRate = sortedRates && sortedRates.length > 0 ? sortedRates[0]?.rate || "N/A" : "N/A";
  const trayPrice = latestRate !== "N/A" ? (parseFloat(latestRate) * 30).toFixed(2) : "N/A";
  
  // Format date for display
  const formattedDate = sortedRates && sortedRates.length > 0 && sortedRates[0]?.date 
    ? new Date(sortedRates[0].date).toLocaleDateString('en-US', { 
        year: 'numeric', 
        month: 'long', 
        day: 'numeric' 
      })
    : "N/A";
  
  // Create LocalBusiness schema
  const localBusinessSchema = {
    "@context": "https://schema.org",
    "@type": "LocalBusiness",
    "name": `Egg Market ${selectedCity}`,
    "description": `Find today's egg rates in ${selectedCity}, ${selectedState}. Updated daily NECC egg prices and wholesale egg rates.`,
    "address": {
      "@type": "PostalAddress",
      "addressLocality": selectedCity,
      "addressRegion": selectedState,
      "addressCountry": "IN"
    },
    "image": "https://todayeggrates.com/eggpic.png",
    "priceRange": "₹₹",
    "telephone": "+91-XXXXXXXXXX",
    "url": `https://todayeggrates.com/${selectedCity.toLowerCase()}-egg-rate`,
    "aggregateRating": {
      "@type": "AggregateRating",
      "ratingValue": "4.8",
      "ratingCount": "254"
    },
    "openingHoursSpecification": [
      {
        "@type": "OpeningHoursSpecification",
        "dayOfWeek": [
          "Monday",
          "Tuesday",
          "Wednesday",
          "Thursday",
          "Friday",
          "Saturday",
          "Sunday"
        ],
        "opens": "06:00",
        "closes": "21:00"
      }
    ],
    "offers": {
      "@type": "Offer",
      "itemOffered": {
        "@type": "Product",
        "name": "Eggs",
        "description": `Fresh eggs in ${selectedCity}, ${selectedState}`,
        "offers": {
          "@type": "Offer",
          "price": latestRate,
          "priceCurrency": "INR",
          "availability": "https://schema.org/InStock",
          "priceValidUntil": new Date(new Date().setDate(new Date().getDate() + 1)).toISOString().split('T')[0]
        }
      }
    }
  };
  
  // Create Product schema
  const productSchema = {
    "@context": "https://schema.org",
    "@type": "Product",
    "name": `Eggs in ${selectedCity}, ${selectedState}`,
    "description": `Fresh eggs available in ${selectedCity}, ${selectedState}. Check today's egg price.`,
    "image": "https://todayeggrates.com/eggpic.png",
    "offers": {
      "@type": "Offer",
      "url": `https://todayeggrates.com/${selectedCity.toLowerCase()}-egg-rate`,
      "priceCurrency": "INR",
      "price": latestRate,
      "priceValidUntil": new Date(new Date().setDate(new Date().getDate() + 1)).toISOString().split('T')[0],
      "availability": "https://schema.org/InStock",
      "itemCondition": "https://schema.org/NewCondition"
    },
    "aggregateRating": {
      "@type": "AggregateRating",
      "ratingValue": "4.8",
      "reviewCount": "254"
    }
  };
  
  // Now add the check for empty data before returning JSX
  if (!eggRates || eggRates.length === 0) {
    return (
      <div className="p-6 bg-gray-50 rounded-lg shadow-md">
        <h2 className="text-2xl font-semibold text-center text-gray-800">
          Egg Rate in {selectedCity}, {selectedState}
        </h2>
        <p className="text-center text-gray-600 mt-4">Loading egg rates...</p>
      </div>
    );
  }

  return (
    <>
      <Helmet>
        <script type="application/ld+json">
          {JSON.stringify(localBusinessSchema)}
        </script>
        <script type="application/ld+json">
          {JSON.stringify(productSchema)}
        </script>
      </Helmet>
      
      <div className="p-6 bg-gray-50 rounded-lg shadow-md">
        <h2 className="text-2xl font-semibold text-center text-gray-800 mb-6">
          Today's Egg Rate in {selectedCity}, {selectedState}
        </h2>
        
        <div className="flex flex-wrap justify-between mb-8">
          <div className="w-full md:w-1/2 p-4">
            <div className="bg-white p-6 rounded-lg shadow-sm">
              <h3 className="text-xl font-medium text-gray-700">Latest Price Details</h3>
              <div className="mt-4 space-y-3">
                <div className="flex justify-between items-center">
                  <span className="text-gray-600">Current Rate:</span>
                  <span className="font-bold text-2xl text-red-600">₹{latestRate} per egg</span>
                </div>
                <div className="flex justify-between items-center">
                  <span className="text-gray-600">Tray Price (30 eggs):</span>
                  <span className="font-semibold text-xl text-blue-600">₹{trayPrice}</span>
                </div>
                <div className="flex justify-between items-center">
                  <span className="text-gray-600">Updated on:</span>
                  <span className="text-gray-800">{formattedDate}</span>
                </div>
              </div>
            </div>
          </div>
          
          <div className="w-full md:w-1/2 p-4">
            <div className="bg-white p-6 rounded-lg shadow-sm h-full">
              <h3 className="text-xl font-medium text-gray-700 mb-4">Price Trend</h3>
              <Line data={chartData} options={chartOptions} />
            </div>
          </div>
        </div>
        
        <h3 className="text-xl font-medium text-gray-800 mb-4">Historical Egg Rates - {selectedCity}</h3>
        <div className="overflow-x-auto">
          <table className="min-w-full bg-white">
            <thead>
              <tr className="bg-gray-200">
                <th className="py-3 px-4 text-left">Date</th>
                <th className="py-3 px-4 text-left">Rate (₹ per egg)</th>
                <th className="py-3 px-4 text-left">Tray Price (₹ for 30 eggs)</th>
              </tr>
            </thead>
            <tbody>
              {displayRates.map((rate, index) => {
                const dateObj = new Date(rate.date);
                const formattedDate = !isNaN(dateObj.getTime()) 
                  ? dateObj.toLocaleDateString('en-US', {
                      year: 'numeric',
                      month: 'short',
                      day: 'numeric'
                    }) 
                  : rate.date;
                
                return (
                  <tr key={index} className={index % 2 === 0 ? 'bg-gray-50' : 'bg-white'}>
                    <td className="py-3 px-4">{formattedDate}</td>
                    <td className="py-3 px-4">₹{rate.rate}</td>
                    <td className="py-3 px-4">₹{(rate.rate * 30).toFixed(2)}</td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
        
        <button 
          className="mt-4 px-4 py-2 bg-blue-500 text-white rounded-lg shadow-md hover:bg-blue-600"
          onClick={toggleHistoryDisplay}
        >
          {showFullHistory ? 'Show Last 10 Days' : 'Show Full History'}
        </button>
        
        <div className="mt-8 bg-blue-50 p-4 rounded-lg">
          <h3 className="text-lg font-medium text-blue-800 mb-2">About {selectedCity} Egg Market</h3>
          <p className="text-gray-700 mb-3">
            The egg market in {selectedCity}, {selectedState} follows the general poultry market trends in the region.
            Prices are influenced by factors such as feed costs, seasonal demand, transportation expenses, and overall market conditions.
          </p>
          <p className="text-gray-700">
            {selectedCity}'s egg rates are typically updated daily based on the National Egg Coordination Committee (NECC) publications
            and local market surveys. Our website provides the most current prices to help consumers and traders make informed decisions.
          </p>
        </div>
      </div>
    </>
  );
};

export default RateTable;